.rightbarFriend {
    display: flex;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .rightbarProfileImgContainer {
    margin-right: 10px;
    position: relative;
  }
  
  .rightbarProfileImg {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
  }
  
  .rightbarOnline{
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background-color: #13bf13;
    position: absolute;
    top: -2px;
    right: -4px;
    border: 2px solid #d9d6d6;
  }
  
  .rightbarUsername{
      font-weight: 500;
  }